@font-face {
  font-family: "Varela Round";
  src: local("VarelaRound"),
    url(./assets/fonts/VarelaRound/VarelaRound-Regular.ttf) format("truetype");
}

@font-face {
  font-family: "Arial Rounded Regular";
  src: local("ArialRounded"),
    url(./assets/fonts/ArialRounded/ArialRounded-Regular.ttf) format("truetype");
}

@font-face {
  font-family: "Arial Rounded Bold";
  src: local("ArialRounded"),
    url(./assets/fonts/ArialRounded/ArialRounded-Bold.ttf) format("truetype");
}

body {
  font-family: "Varela Round", sans-serif;
}

* {
  margin: 0;
  padding: 0;
  font-size: 16px;
  box-sizing: border-box;
}
